import React, { useState, useCallback, useRef, useEffect } from "react";
import { Dialog } from "@headlessui/react";
import { Close } from "../../../utils/Icons";
import { isEmpty } from "lodash";
import { toast } from "react-toastify";
import ContentEditable from "react-contenteditable";
import { useDropzone } from "react-dropzone";
import { Camera } from "../../../utils/Icons";
import {
  uploadBannerApi,
  uploadAvatarApi,
  updateInfoApi,
  getAvatarApi,
  getBannerApi,
} from "../../../api/user";
export default function EditUserForm(props) {
  const { user, show, setShow, title } = props;
  const [formData, setFormData] = useState(initialValue(user));
  const ref = useRef(1);
  const [bannerUrl, setBannerUrl] = useState(
    user?.banner ? getBannerApi(user.banner) : null
  );
  const [bannerFile, setBannerFile] = useState(null);
  const [avatarUrl, setAvatarUrl] = useState(
    user?.avatar ? getAvatarApi(user.avatar) : null
  );
  const [avatarFile, setAvatarFile] = useState(null);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const onDropBanner = useCallback((acceptedFile) => {
    const file = acceptedFile[0];
    setBannerUrl(URL.createObjectURL(file));
    setBannerFile(file);
  });
  const {
    getRootProps: getRootBannerProps,
    getInputProps: getInputBannerProps,
  } = useDropzone({
    accept: "image/jpeg, image/png",
    noKeyboard: true,
    multiple: false,
    onDrop: onDropBanner,
  });
  useEffect(() => {
    setFormData(initialValue(user));
    setBannerUrl(user?.banner ? getBannerApi(user.banner) : null);
    setAvatarUrl(user?.avatar ? getAvatarApi(user.avatar) : null);
  }, [user]);
  const [loading, setLoading] = useState(false);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const onDropAvatar = useCallback((acceptedFile) => {
    const file = acceptedFile[0];
    setAvatarUrl(URL.createObjectURL(file));
    setAvatarFile(file);
  });
  const {
    getRootProps: getRootAvatarProps,
    getInputProps: getInputAvatarProps,
  } = useDropzone({
    accept: "image/jpeg, image/png",
    noKeyboard: true,
    multiple: false,
    onDrop: onDropAvatar,
  });
  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  const onSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    if (bannerFile) {
      await uploadBannerApi(bannerFile).catch(() => {
        toast.error("Error al subir el nuevo banner");
      });
    }
    if (avatarFile) {
      await uploadAvatarApi(avatarFile).catch(() => {
        toast.error("Error al subir el nuevo avatar");
      });
    }

    await updateInfoApi(formData)
      .then(() => {
        setShow(false);
      })
      .catch(() => {
        toast.error("Error al actualizar los datos.");
      });
    setLoading(false);
    window.location.reload();
  };

  return (
    <Dialog
      className="fixed inset-0 m-auto flex"
      style={{ maxWidth: 600 }}
      open={show}
      onClose={() => setShow(false)}
      initialFocus={ref}
    >
      <div className="flex items-center justify-center mt-28">
        <Dialog.Title>
          <Close onClick={() => setShow(false)} />
          <span>{title}</span>
        </Dialog.Title>
        <Dialog.Description></Dialog.Description>
        <div className="w-full relative bg-green-500">
          <div
            className="w-full h-60 bg-contain bg-no-repeat bg-center flex items-center rounded"
            style={{ backgroundImage: `url('${bannerUrl}')` }}
            {...getRootBannerProps()}
          >
            <input {...getInputBannerProps()} />
            <Camera className="m-auto" width={20} height={20} />
          </div>
          <div
            className="h-32 w-32 rounded-full bg-cover absolute flex top-1/4 flex-1 left-1 items-center "
            style={{ backgroundImage: `url('${avatarUrl}')` }}
            {...getRootAvatarProps()}
          >
            <input {...getInputAvatarProps()} />
            <Camera className="m-auto" width={20} height={20} />
          </div>
          <form
            onSubmit={onSubmit}
            className="grid grid-flow-row p-4 mt-8 w-full"
          >
            <div className="grid grid-flow-col justify-around m-2">
              <div>
                Nombre:
                <input
                  type="text"
                  className="ml-1 p-1 rounded"
                  placeholder="Nombre"
                  name="name"
                  ref={ref}
                  defaultValue={formData.name}
                  onChange={onChange}
                />
              </div>
              <div>
                Apellido:
                <input
                  type="text"
                  className="ml-1 p-1 rounded"
                  placeholder="Apellidos"
                  name="surnames"
                  defaultValue={formData.surnames}
                  onChange={onChange}
                />
              </div>
            </div>
            <div className=" justify-center ml-5">
              Biographia:
              <ContentEditable
                className="w-full h-full border-none bg-white rounded"
                style={{ textOverflow: "break-word", maxWidth: 530 }}
                placeholder="¿Que estas pensando?"
                html={formData.biography}
                defaultValue={formData.biography}
                onChange={(e) => (formData.biography = e.target.value)}
              />
            </div>
            <div className="grid grid-flow-col justify-around ml-3 mt-9">
              <div>
                Sitio web:
                <input
                  type="text"
                  className="ml-1 p-1 rounded"
                  placeholder="Sitio Web"
                  name="webSite"
                  defaultValue={formData.webSite}
                  onChange={onChange}
                />
              </div>
              <div>
                Ubicación:
                <input
                  type="text"
                  className="ml-1 p-1 rounded"
                  placeholder="Ubicación"
                  name="location"
                  defaultValue={formData.location}
                  onChange={onChange}
                />
              </div>
            </div>
            <button
              className="m-auto flex items-center text-center"
              variant="primary"
              type="submit"
            >
              {loading ? (
                <div className="w-12 h-12 border-8 border-yellow-400 rounded-full loader" />
              ) : (
                "Actualizar"
              )}
            </button>
          </form>
        </div>
      </div>
    </Dialog>
  );
}
function initialValue(user) {
  return {
    name: isEmpty(user?.name) ? "" : user.name,
    surnames: isEmpty(user?.surnames) ? "" : user.surnames,
    biography: isEmpty(user?.biography) ? "" : user.biography,
    location: isEmpty(user?.location) ? "" : user.location,
    webSite: isEmpty(user?.webSite) ? "" : user.webSite,
    birthDate: isEmpty(user?.birthDate) ? "" : user.birthDate,
  };
}
