import React, { useState } from "react";
import { values, size, toNumber } from "lodash";
import { toast } from "react-toastify";
import { Dialog } from "@headlessui/react";
import { isEmailValid } from "../../utils/validations";
import { signUpApi } from "../../api/auth";

export default function SignUpForm(props) {
  const { show, setShow } = props;
  const [formData, setFormData] = useState(initialFormValue());
  const [loading, setLoading] = useState(false);
  const onSubmit = (e) => {
    e.preventDefault();

    let validCount = 0;
    values(formData).some(() => {
      validCount++;
      return null;
    });

    if (validCount !== size(formData)) {
      toast.warning("Completa todos los campos del formulario.");
    } else {
      if (!isEmailValid(formData.email)) {
        toast.warning("Email invalido");
      } else if (formData.password !== formData.repeatpassword) {
        toast.warning("Las contraseñas tienen que ser iguales.");
      } else if (size(formData.password) < 6) {
        toast.warning("La contraseña tiene que tener al menos 6 caracteres.");
      } else if (toNumber(formData.updateDate)) {
        toast.warning(
          "La actualización de las notificaciones tiene que ser 0 o mayor a 0"
        );
      } else {
        setLoading(true);
        signUpApi(formData)
          .then((response) => {
            if (response.code) {
              toast.warning(response.message);
            } else {
              toast.success("El registro ha sido correcto");
              setShow(false);
              setFormData(initialFormValue());
            }
          })
          .catch(() => {
            toast.error("Error del servidor, intentelo mas tarde!");
          })
          .finally(() => {
            setLoading(false);
          });
      }
    }
  };
  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  return (
    <Dialog
      className="fixed z-10 inset-0 overflow-y-auto"
      open={show}
      onClose={() => setShow(false)}
    >
      <div className="flex items-center justify-center min-h-screen">
        <Dialog.Overlay className=" inset-0 bg-black opacity-30" />
        <Dialog.Title></Dialog.Title>
        <Dialog.Description></Dialog.Description>
        <div className="w-200 h-200 bg-blue-500 flex items-center justify-center p-6 rounded">
          <form onSubmit={onSubmit} className="text-center">
            <h2 className="text-xl font-bold">Crea tu cuenta</h2>
            <div className="grid md:grid-flow-col sm:grid-flow-row">
              <div>
                <input
                  className="block text-lg p-1 m-2 rounded bg-gray-300 focus:outline-none focus:bg-gray-100 focus:border-gray-600 border border-gray-900"
                  type="text"
                  placeholder="Nombre"
                  value={formData.name}
                  name="name"
                  onChange={onChange}
                />
              </div>
              <div>
                <input
                  className="block text-lg p-1 m-2 rounded bg-gray-300 focus:outline-none focus:bg-gray-100 focus:border-gray-600 border border-gray-900"
                  type="text"
                  placeholder="Apellidos"
                  name="surnames"
                  value={formData.surnames}
                  onChange={onChange}
                />
              </div>
            </div>
            <div className="grid md:grid-flow-col sm:grid-flow-row">
              <input
                className="block text-lg p-1 m-2 rounded bg-gray-300 focus:outline-none focus:bg-gray-100 focus:border-gray-600 border border-gray-900"
                type="email"
                placeholder="Correo electronico"
                name="email"
                value={formData.email}
                onChange={onChange}
              />
            </div>

            <div className="grid md:grid-flow-col sm:grid-flow-row">
              <div>
                <input
                  className="block text-lg p-1 m-2 rounded bg-gray-300 focus:outline-none focus:bg-gray-100 focus:border-gray-600 border border-gray-900"
                  type="password"
                  placeholder="Contraseña"
                  name="password"
                  value={formData.password}
                  onChange={onChange}
                />
              </div>
              <div>
                <input
                  className="block text-lg p-1 m-2 rounded bg-gray-300 focus:outline-none focus:bg-gray-100 focus:border-gray-600 border border-gray-900"
                  type="password"
                  placeholder="Repetir contraseña"
                  name="repeatpassword"
                  value={formData.repeatpassword}
                  onChange={onChange}
                />
              </div>
            </div>

            <button
              className="border p-1 rounded border-gray-100 text-gray-100"
              type="submit"
            >
              {!loading ? (
                "Registrarse"
              ) : (
                <div className="w-12 h-12 border-8 border-yellow-400 rounded-full loader" />
              )}
            </button>
          </form>
        </div>
      </div>
    </Dialog>
  );
}

function initialFormValue() {
  return {
    name: "",
    surnames: "",
    email: "",
    updateDate: 0,
    password: "prueba1",
    repeatpassword: "prueba1",
  };
}
