import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { logoutApi } from "../../api/auth";
import atlas from "../../assets/png/atlas.png";
import { MenuAlt2Icon } from "@heroicons/react/solid";
import { getUserApi, getAvatarApi } from "../../api/user";
import AvatarNotFound from "../../assets/png/avatar-no-found.png";
import {
  isStandardApi,
  updateDateApi,
  isAdminApi,
  isSuperAdminApi,
} from "../../api/date";
import useAuth from "../../hooks/useAuth";
import { Menu, Transition } from "@headlessui/react";
import { TootModal } from "../../components";

export default function LeftMenu(props) {
  const { setRefreshCheckLogin } = props;
  const [showModal, setShowModal] = useState(false);
  const [isStandard, setIsStandard] = useState(true);
  const [avatarUrl, setAvatarUrl] = useState(null);
  const [isAdmin, setIsAdmin] = useState(false);
  const [isSuperAdmin, setIsSuperAdmin] = useState(false);
  const user = useAuth();
  useEffect(() => {
    isStandardApi().then((response) => {
      setIsStandard(response.status);
    });
    isAdminApi().then((response) => {
      setIsAdmin(response.status);
    });
    isSuperAdminApi().then((response) => {
      setIsSuperAdmin(response.status);
    });
  }, []);
  useEffect(() => {
    console.log(showModal);
  }, [showModal]);
  useEffect(() => {
    let isMounted = true;
    getUserApi(user?._id).then((response) => {
      if (isMounted) {
        setAvatarUrl(
          response?.avatar ? getAvatarApi(response.avatar) : AvatarNotFound
        );
      }
    });
  }, [user]);
  const logout = () => {
    logoutApi();
    setRefreshCheckLogin(true);
  };

  return (
    <>
      <nav className="bg-gray-100 items-center" style={{ maxHeight: 70 }}>
        <div>
          <div className="flex justify-around">
            <div className="hidden md:flex items-center space-x-1">
              <Link
                to="/"
                className="py-5 px-3 text-gray-700 hover:text-gray-900"
              >
                Inicio
              </Link>
              <Link
                to="/users"
                className="py-5 px-3 text-gray-700 hover:text-gray-900"
              >
                Buscar usuarios
              </Link>
              <button
                className="flex items-center space-x-1"
                onClick={() => setShowModal(true)}
              >
                Toot
              </button>
            </div>

            <Menu
              as="div"
              className="md:hidden relative inline-block text-left py-5"
            >
              {({ open }) => (
                <>
                  <div>
                    <Menu.Button>
                      <MenuAlt2Icon className="h-5 w-5 text-black" />
                    </Menu.Button>
                  </div>
                  <Transition
                    show={open}
                    enter="transition duration-100 ease-out"
                    enterFrom="transform scale-95 opacity-0"
                    enterTo="transform scale-100 opacity-100"
                    leave="transition duration-75 ease-out"
                    leaveFrom="transform scale-100 opacity-100"
                    leaveTo="transform scale-95 opacity-0"
                  >
                    <Menu.Items
                      static
                      className="absolute left-0 w-56 mt-2 origin-top-left bg-white divide-y divide-gray-100 rounded-md shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none grid grid-flow-row"
                    >
                      <Menu.Item>
                        <Link
                          to="/"
                          className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                        >
                          Inicio
                        </Link>
                      </Menu.Item>
                      <Menu.Item>
                        <Link
                          to="/users"
                          className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                        >
                          Buscar usuarios
                        </Link>
                      </Menu.Item>
                      <Menu.Item>
                        <button
                          className="py-5 px-3 text-left text-gray-700 hover:bg-gray-200"
                          onClick={() => setShowModal(true)}
                        >
                          Toot
                        </button>
                      </Menu.Item>
                    </Menu.Items>
                  </Transition>
                </>
              )}
            </Menu>

            <div className="">
              <Link
                to="/"
                className="flex items-center py-5 px-2 text-gray-700 hover:bg-gray-200"
              >
                <img src={atlas} width={20} alt="" />{" "}
                <span className="font-bold">Lisander</span>
              </Link>
            </div>

            <Menu as="div" className="relative inline-block text-left py-5">
              {({ open }) => (
                <>
                  <div>
                    <Menu.Button>
                      <img
                        alt={""}
                        width={40}
                        className=" rounded-full"
                        src={avatarUrl}
                      />
                    </Menu.Button>
                  </div>
                  <Transition
                    show={open}
                    enter="transition duration-100 ease-out"
                    enterFrom="transform scale-95 opacity-0"
                    enterTo="transform scale-100 opacity-100"
                    leave="transition duration-75 ease-out"
                    leaveFrom="transform scale-100 opacity-100"
                    leaveTo="transform scale-95 opacity-0"
                  >
                    <Menu.Items
                      static
                      className="absolute right-0 w-56 mt-2 origin-top-right bg-white divide-y divide-gray-100 rounded-md shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none grid grid-flow-row"
                    >
                      <Menu.Item>
                        <Link
                          to={`/${user?._id}`}
                          className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                        >
                          Perfil
                        </Link>
                      </Menu.Item>
                      <Menu.Item>
                        <Link
                          to="/follows"
                          className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                        >
                          Seguidores/Seguidos
                        </Link>
                      </Menu.Item>

                      <Menu.Item>
                        <Link
                          to={`/blocked`}
                          className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                        >
                          Blockeados
                        </Link>
                      </Menu.Item>
                      {!isStandard && (
                        <Menu.Item>
                          <Link
                            to=""
                            onClick={() => updateDateApi()}
                            className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                          >
                            Actualizar
                          </Link>
                        </Menu.Item>
                      )}
                      {isAdmin && (
                        <Menu.Item>
                          <Link
                            to={`/admin/reports`}
                            className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                          >
                            Reportes
                          </Link>
                        </Menu.Item>
                      )}
                      {isSuperAdmin && (
                        <Menu.Item>
                          <Link
                            to={`/admin/form`}
                            className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                          >
                            Crear Administradores
                          </Link>
                        </Menu.Item>
                      )}
                      <Menu.Item>
                        <Link
                          to=""
                          onClick={logout}
                          className="py-5 px-3 text-gray-700 hover:bg-gray-200"
                        >
                          Cerrar sesion
                        </Link>
                      </Menu.Item>
                    </Menu.Items>
                  </Transition>
                </>
              )}
            </Menu>
          </div>
        </div>
      </nav>
      <div className="py-23 text-center">
        <TootModal show={showModal} setShow={setShowModal}></TootModal>
      </div>
    </>
  );
}
