import React, { useState, useRef } from "react";
import { Dialog } from "@headlessui/react";
import { Close } from "../../../utils/Icons";
import { addTootApi } from "../../../api/toot";
import ContentEditable from "react-contenteditable";
import { toast } from "react-toastify";

export default function TootModal(props) {
  const { show, setShow } = props;
  const [message, setMessage] = useState("");
  const ref = useRef(1);
  const maxLength = 250;
  const onSubmit = (e) => {
    e.preventDefault();
    if (message.length > 0 && message.length <= maxLength)
      addTootApi(message)
        .then((response) => {
          if (response?.code >= 200 && response?.code < 300) {
            toast.success(response.message);
            setShow(false);
            window.location.reload();
          }
        })
        .catch(() => {
          toast.warning("Error al enviar el toot, intentelo más tarde");
        });
  };
  //logo
  return (
    <Dialog
      className="fixed inset-0 m-auto bg-blue-500"
      style={{ maxWidth: 500, height: 200 }}
      open={show}
      onClose={() => setShow(false)}
      initialFocus={ref}
    >
      <div className="flex justify-center  bg-blue-500">
        <Dialog.Overlay className=" inset-0 bg-black opacity-30" />

        <Dialog.Title>
          <Close onClick={() => setShow(false)}></Close>
        </Dialog.Title>
        <Dialog.Description></Dialog.Description>
        <form
          onSubmit={onSubmit}
          className=" grid grid-flow-row m-auto w-full h-full p-4"
        >
          <div className="rounded-sm p-1" ref={ref}>
            <ContentEditable
              className="w-full h-full border-none bg-white"
              style={{ textOverflow: "break-word", maxWidth: 460 }}
              placeholder="¿Que estas pensando?"
              html={message}
              onChange={(e) => setMessage(e.target.value)}
            />
            <span className={message.length > maxLength ? "text-red-600" : ""}>
              {
                message
                  .replace("<div>", "")
                  .replace("</di>", "")
                  .replace(/&nbsp;/g, " ").length
              }
            </span>
          </div>

          <button
            type="submit"
            disabled={
              message
                .replace("<div>", "")
                .replace("</di>", "")
                .replace(/&nbsp;/g, " ").length > maxLength ||
              message
                .replace("<div>", "")
                .replace("</di>", "")
                .replace(/&nbsp;/g, " ").length < 1
            }
          >
            Toot
          </button>
        </form>
      </div>
    </Dialog>
  );
}
