import React, {useRef} from 'react';
import { Dialog } from "@headlessui/react";
export default function TButtonModal(props){
    const {show, setShow, messageText, acceptText, cancelText, onAccept, onCancel} = props;
    const ref = useRef(1);
    //logo
    return (
        <Dialog className="fixed inset-0 m-auto bg-blue-500 w-full h-1/6" style={{maxWidth:500,}} open={show} onClose={()=>setShow(false)}>
            <div className="bg-blue-500">
                <Dialog.Overlay className=" inset-0 bg-black opacity-30"/>
                <Dialog.Title>
                   
                </Dialog.Title>
                <Dialog.Description>
                        <div className="grid grid-flow-row m-auto text-center w-full h-full p-4"  ref={ref}  >
                        {messageText}
                        </div>
                        <div className="flex justify-around w-full">
                            <button className="" onClick={onAccept}>{acceptText}</button> <button className="" onClick={onCancel}>{cancelText}</button>
                        </div>
                </Dialog.Description>
            </div>
        </Dialog>
    );
}