import React, { useState, useEffect } from "react";
import { map } from "lodash";
import { getUserApi, getAvatarApi } from "../../api/user";
import AvatarNotFound from "../../assets/png/avatar-no-found.png";
import ContentEditable from "react-contenteditable";
import { Menu, Transition } from "@headlessui/react";
import { DotsVerticalIcon } from "@heroicons/react/solid";
import { deleteTootApi } from "../../api/toot";

//import {replaceURLWithHTMLLinks} from "../../utils/functions";
import { formatRelative, parseISO } from "date-fns";
import { addCommentApi } from "../../api/comment";
import { ChatIcon } from "@heroicons/react/solid";
import { Link } from "react-router-dom";
import classNames from "classnames";
import { toast } from "react-toastify";
import { es } from "date-fns/locale";
export default function ListToots(props) {
  const { toots, loggedUser } = props;

  return (
    <div className="w-full text-center">
      {!toots && "No hay toots que mostrar."}
      {map(toots, (toot, index) => {
        return <Toot key={index} toot={toot} loggedUser={loggedUser} />;
      })}
    </div>
  );
}
function Toot(props) {
  const { toot, loggedUser } = props;
  const [userInfo, setUserInfo] = useState(null);
  const [avatarUrl, setAvatarUrl] = useState(null);
  useEffect(() => {
    let isMounted = true;
    toot &&
      getUserApi(toot.userId).then((response) => {
        if (isMounted) {
          setUserInfo(response);
          setAvatarUrl(
            response?.avatar ? getAvatarApi(response.avatar) : AvatarNotFound
          );
        }
      });
    return () => {
      isMounted = false;
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [toot]);
  const [message, setMessage] = useState("");
  const maxLength = 250;
  const onSubmit = (e) => {
    e.preventDefault();
    if (message.length > 0 && message.length <= maxLength)
      addCommentApi(message, toot._id)
        .then((response) => {
          if (response?.code >= 200 && response?.code < 300) {
            toast.success(response.message);
            setMessage("");
          }
        })
        .catch(() => {
          toast.warning("Error al enviar el comentario, intentelo más tarde");
        });
  };

  return (
    <div className="grid grid-flow-row border p-1 w-full">
      <div className="grid grid-flow-col justify-start w-full">
        <Link to={`/${userInfo?.id}`}>
          <img
            alt={userInfo?.name + " " + userInfo?.surnames}
            className="w-20 h-20 p-1"
            src={avatarUrl}
          />
        </Link>

        <div className="ml-3 grid grid-flow-row ">
          <div className=" flex items-center w-full">
            {loggedUser?._id === toot?.userId && (
              <Menu as="span" className="mt-2">
                {({ open }) => (
                  <>
                    <div>
                      <Menu.Button>
                        <DotsVerticalIcon className="h-5 w-5 text-black" />
                      </Menu.Button>
                    </div>

                    <Transition
                      show={open}
                      enter="transition duration-100 ease-out"
                      enterFrom="transform scale-95 opacity-0"
                      enterTo="transform scale-100 opacity-100"
                      leave="transition duration-75 ease-out"
                      leaveFrom="transform scale-100 opacity-100"
                      leaveTo="transform scale-95 opacity-0"
                    >
                      <Menu.Items
                        static
                        className="absolute w-56 mt-2 origin-top-right bg-white divide-y divide-gray-100 rounded-md shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none grid grid-flow-row"
                      >
                        <Menu.Item>
                          <button
                            onClick={() => {
                              deleteTootApi(toot.id ? toot.id : toot._id).then(
                                (response) => toast.success(response.message)
                              );
                              window.location.reload();
                            }}
                          >
                            Eliminar toot
                          </button>
                        </Menu.Item>
                      </Menu.Items>
                    </Transition>
                  </>
                )}
              </Menu>
            )}
            <Link
              className="font-bold flex items-center"
              to={`/${userInfo?.id}`}
            >
              {userInfo?.name} {userInfo?.surnames}
            </Link>
            <Link
              className="ml-2 text-gray-500"
              to={`/toot/${toot.id ? toot.id : toot._id}`}
            >
              {formatRelative(parseISO(toot.date), new Date(), {
                locale: es,
              })}
            </Link>
          </div>

          <div className=" w-full justify-start flex">
            <div
              className="w-full block border-transparent bg-transparent"
              style={{
                resize: "none",
                width: "auto",
                overflow: "hidden",
                overflowWrap: "break-word",
                textOverflow: "break-word",
                maxWidth: 460,
              }}
              dangerouslySetInnerHTML={{ __html: toot.message }}
              readOnly={true}
            />
          </div>
        </div>
        <div className="justify-end w-full"></div>
      </div>
      <form
        onSubmit={onSubmit}
        className="w-full my-3 flex justify-between items-center"
      >
        <button
          className={classNames("count", { error: message.length > maxLength })}
        >
          {
            message
              .replace("<div>", "")
              .replace("</di>", "")
              .replace(/&nbsp;/g, " ").length
          }
        </button>
        <ContentEditable
          className="w-full h-full border-none text-left bg-white"
          style={{ textOverflow: "break-word", maxWidth: 500 }}
          placeholder="¿Que estas pensando?"
          html={message}
          onChange={(e) => setMessage(e.target.value)}
        />

        <button
          className="p-1"
          type="submit"
          disabled={message.length > maxLength || message.length < 1}
        >
          <ChatIcon className="w-7 h-7 text-gray-700" />
        </button>
      </form>
    </div>
  );
}
//dangerouslySetInnerHTML={{__html: replaceURLWithHTMLLinks(toot.message)}}
